.modal-wrapper {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    background-color: rgba(189, 189, 189, 0.9);
    z-index: 2;
}

.modal-container {
    top: 0;
    position: fixed;
    z-index: 3;
}

