}

.quiz-name-type {
    background: white;
    margin: 100px 500px;
    width: 500px;
    border-radius: 10px;
    padding: 20px;
    font-size: 25px;
    color: #757575
}

.quiz-name p {
    text-align: center;
    margin: 8px;
}

input::placeholder {
    font-size: 25px;
}

.quiz-name-type input {
    width: 438px;
    border-radius: 10px;
    height: 50px;
    background-color: #ffffff;
    border: none;
    box-shadow: 0px 0px 10px grey;
}

.quiz-name {
    margin: 10px 26px;
    width: 212px;
    text-align: left;
    font-size: 23px;
    color: #757575
}

.display-flex {
    display: flex;
    justify-content: space-around;
}

.buttons {
    font-size: 18px;
}

.buttons button {
    box-shadow: 0px 0px 5px grey;
    border: none;
    background-color: white;
    border-radius: 10px;
    height: 35px;
    margin: 14px 3px;
    width: 133px;
    font-size: 15px;
    color: #757575;
}

.action-buttons button {
    border: none;
    border-radius: 10px;
    width: 184px;
    margin: 10px 33px;
    height: 44px
}

.cancel {
    box-shadow: 0px 0px 10px grey;
    background-color: white;

}

.continue {
    background-color: #60B84B;

}

